import React, { useState } from 'react';
import Styles from "./Form.module.scss";
import Backdrop from "../../Component/Backdrop/Backdrop";
import { useDispatch } from "react-redux";
import { actionTypes } from "../../store/DataStore";

function Form () {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();  
  const backdropCloseHandler = () =>{
    dispatch(actionTypes.isFormClose());
    }
  
  const usernameHandler =(event) => {
       setUsername(event.target.value);
  }
  const passwordHandler =(event) => {
    setPassword(event.target.value);
  }
  const createAdminHandler = () =>{
       console.log(username, password, "CREATE ADMIN FORM SUBMIT")
  }   
  const formComponent = () => {
    return (
        <form className={Styles.form}>
            <div className={Styles.formInput}>
                <label htmlFor='email'>Username</label>
                <input id='email' type="email" placeholder='Username' value={username} onChange={usernameHandler}/>
            </div>
            <div className={Styles.formInput}>
            <label htmlFor='password'>Password</label>   
            <input id='password' type="text" placeholder='Password' value={password} onChange={passwordHandler}/>
            </div>
            <div className={Styles.buttonForm}>
                 <button onClick={createAdminHandler}>Create</button>
                 <button onClick={backdropCloseHandler}>Cancel</button>
            </div>
        </form>
    );
  };
  return (
    <Backdrop>
    <div className={Styles.main_form_container}>
        <h3>Create new Admin</h3>
         {formComponent()}
    </div>
    </Backdrop> 
  )
}

export default Form;
