import React, { useEffect } from 'react';
import Styles from "./Admin.module.scss"
import List from "../List/List"
import { ErrorBoundary } from 'react-error-boundary';
import Error from '../../Component/Error/Error';
import { useDispatch } from "react-redux";
import { actionTypes} from '../../store/DataStore';
import { useSelector } from 'react-redux';
const data = [
    {
        email: 'admin1@example.com',
        id: 1
    }
    ,
    {
        email: 'admin2@example.com',
        id: 2
    },
    {
        email: 'admin3@example.com',
        id: 3
    },
    {
        email: 'admin4@example.com',
        id: 4
    },
    {
        email: 'admin5@example.com',
        id: 5
    }
]

function Admin() {
  const dispatch = useDispatch();
  const context = useSelector(state => state.isLog);
  const { adminList } = context;
  console.log(adminList, "IN ADMIN")

  useEffect(()=>{
      dispatch(actionTypes.addAdmin({ payload: data }));
  }, [dispatch]);
    ///Fetch all the admins list and send to the list
  return (
    <div className={Styles.admin}>
        <ErrorBoundary fallback={<Error error={"Error from Admin."}/>}>
                 {adminList.length> 0 && <List list={adminList} />}
                 {adminList.length === 0 && <p>No Admins to show!!</p>}
        </ErrorBoundary>
    </div>
  )
}

export default Admin;