import React, { Suspense } from 'react';
import Header from "../Header/Header"
import Styles from "./MainView.module.scss"
import { ErrorBoundary } from 'react-error-boundary';
import Error from '../../Component/Error/Error';
import { motion } from 'framer-motion';
import Admin from "../Admin/Admin";
import Form from "../Form/Form";
import { useSelector } from 'react-redux';
// import { Apis } from '../../APIs/API';
// const apis = new Apis("http://localhost:4000/root");



function MainView() {
  const context = useSelector(state => state.isLog);
  const { adminForm } = context;
  return (
    <Suspense fallback={<h1>{"Loading Dashboard..."}</h1>}>
    <motion.div
    initial={{width: "200rem"}}
    animate={{width : '70%'}}
    transition={{type: "spring", duration: 1}}
    >
      <div className={Styles.mainviewcontainer}>
      <ErrorBoundary fallback={<Error error="Couldn't load from Main View."/>}>
       <Header/>
       {!adminForm && <Admin/>}
       {adminForm && <Form/>}
      </ErrorBoundary>      
    </div>
    </motion.div>
    </Suspense>
  )
}

export default MainView