import { createSlice, configureStore } from '@reduxjs/toolkit';

const initialState = {
     isLogSwitch : false,
     adminList:[],
     adminForm: false
}

const toggleSlice = createSlice({
    name : "Login",
    initialState : initialState,
    reducers : {
        isLog(state) {
            state.isLogSwitch = !state.isLogSwitch;
        },
        addAdmin(state, action){
            const { payload } = action;
            state.adminList = [...payload.payload]
        },
        deleteAdmin(state, action) {
            const { payload } = action;
             const newList = state.adminList.filter(admin => {
                   return admin.id !== payload.payload; 
            });
            state.adminList = [...newList];
        },
        isFormOpen(state) {
            state.adminForm = true;
        },
        isFormClose(state){
            state.adminForm = false;
        }
    }
});

const store = configureStore({
     reducer : { isLog : toggleSlice.reducer }
})

export default store;

export const actionTypes = toggleSlice.actions;

