import React from 'react';
import Styles from "./Error.module.scss";
import ErrorIcon from "../Icons/Warning";
import { Box } from "@mui/material"

function Error(props) {
  return (
    <Box className={Styles.error}  sx={{
       margin : "1em",
       padding : ".5em",
       display: "flex",
       justifyContent: "space-evenly",
       alignItems: "center",
       width : "90%",
       fontFamily : "inherit",
       fontWeight : 300
    }}>
      <ErrorIcon/>
      <h4>{props.error}</h4>
    </Box>
  )
}

export default Error