import React from "react";
import Styles from "./Background.module.scss";
import Waves from "../../Component/Icons/Waves";
import MailIcon from "../../Component/Icons/MailIcon";

function Background (props) {
   return (
    <div>
        <MailIcon toggle={props.toggle} mailStyle={Styles.mailIcon}/>
        <Waves waves={Styles.waves} waves1={Styles.waves1} waves2={Styles.waves2}/>
    </div>
   )
}

export default Background;