import React from 'react';
import Styles from './Header.module.scss';
import { useSelector, useDispatch } from "react-redux"
import { actionTypes } from "../../store/DataStore";
import { ErrorBoundary } from 'react-error-boundary';
import Error from '../../Component/Error/Error';

function Header(props) {
    const context = useSelector(state => state.isLog);
    const dispatch = useDispatch();
    const { isLogSwitch } = context;
    const openAddUserFormHandler = ()=>{
          dispatch(actionTypes.isFormOpen());
    }
    const logoutHandler = () => {
        dispatch(actionTypes.isLog());
    }
   
   
  return (
    <div className={Styles.header}>
        <ErrorBoundary fallback={<Error error={"Error is coming from Header."}/>}>
            <div className={isLogSwitch? Styles["on"] : Styles["off"]}>
                <span></span>
            </div>
            <div className={Styles.header_inner_body}>
                 <p>Super Admin Portal</p>
            </div>
            <div className={Styles.header_inner_footer}>
                <ul>
                    <li><button onClick={openAddUserFormHandler}>Add User</button></li>
                    <li><button onClick={logoutHandler}>Logout</button></li>
                </ul>
            </div>
        </ErrorBoundary>
    </div>
  )
}

export default Header